import Vue from 'vue';
import Router from 'vue-router';

import Home from '@/containers/Home';
import AGLukashenko from '@/containers/peoples/AGLukashenko';

Vue.use(Router);

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      alias: '/home',
      name: 'Home',
      component: Home,
    },
    {
      path: '/AGL',
      name: 'AGLukashenko',
      component: AGLukashenko,
    },
  ],
});



